import React from 'react';

import styles from './Reviews.module.scss';
import star from 'public/static/img/star.svg';
import darkStar from 'public/static/img/darkStar.svg';
import Image from 'next/image';
import { useTranslation } from 'next-i18next';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { useSelector } from 'react-redux';
import {
	changeCounter,
	changeCurrentPage,
	changeReviewList,
	changeTotalPages,
	changeTotalStars,
	getReviewList,
} from 'store/product/ReviewListStore';
import { useRouter } from 'next/router';
import Loader from 'components/UI/Loader';

type ReviewsTypes = {
	counter: number;
	totalStars: number;
	totalPages: number;
	reviewsList: {
		id: number;
		name: string;
		text: string;
		stars: number;
	}[];
};

const Reviews = ({ counter, totalStars, totalPages, reviewsList }: ReviewsTypes) => {
	const [isOpenReviews, setIsOpenReviews] = React.useState<boolean>(false);

	const { t } = useTranslation();

	const dispatch = useAppDispatch();

	const router = useRouter();

	const reviewList = useSelector((state: any) => state.review.reviewList);
	const counterData = useSelector((state: any) => state.review.counter);
	const totalStarsData = useSelector((state: any) => state.review.totalStars);
	const totalPagesData = useSelector((state: any) => state.review.totalPages);
	const currentPage = useSelector((state: any) => state.review.currentPage);
	const reviewIsLoaded = useSelector((state: any) => state.review.reviewIsLoaded);

	React.useEffect(() => {
		dispatch(changeReviewList({ reviewList: reviewsList }));
		dispatch(changeCounter(counter));
		dispatch(changeTotalStars({ totalStars: totalStars }));
		dispatch(changeTotalPages(totalPages));
	}, []);

	React.useEffect(() => {
		dispatch(changeCurrentPage(1));
	}, [router]);

	const isToggleReviewsHandler = isOpen => {
		setIsOpenReviews(isOpen);
	};

	const paginationClickHandler = (page: number) => {
		dispatch(
			getReviewList({
				id: parseInt(router.query.name as string),
				page: page,
				pageSize: 5,
			})
		);
		dispatch(changeCurrentPage(page));
	};

	return (
		<div className={styles.reviews}>
			<>
				<div className={styles.titleRow}>
					<p className={styles.title}>{t('product:reviews')}</p>
					<div className={styles.total}>
						<p>{`(${counterData})`}</p>
						<div className={styles.totalStars}>
							{[...Array(5)].map((item, index) => {
								if (index <= totalStarsData - 1) {
									return (
										<Image
											alt="darkStar"
											key={index}
											src={darkStar}
											width={16}
											height={16}
											style={{
												maxWidth: '100%',
												height: 'auto',
											}}
										/>
									);
								} else {
									return (
										<Image
											alt="star"
											key={index}
											src={star}
											width={16}
											height={16}
											style={{
												maxWidth: '100%',
												height: 'auto',
											}}
										/>
									);
								}
							})}
						</div>
					</div>
				</div>
				<div className={styles.reviewsList}>
					{reviewList &&
						(isOpenReviews ? reviewList : [reviewList[0]]).map(item => {
							return (
								<div className={styles.review} key={item.id}>
									<div>
										<p className={styles.name}>{item.name}</p>
										<div className={styles.totalStars}>
											{[...Array(5)].map((itemStar, index) => {
												if (index <= item.stars - 1) {
													return (
														<Image
															alt="darkStar"
															key={index}
															src={darkStar}
															width={16}
															height={16}
															style={{
																maxWidth: '100%',
																height: 'auto',
															}}
														/>
													);
												} else {
													return (
														<Image
															alt="star"
															key={index}
															src={star}
															width={16}
															height={16}
															style={{
																maxWidth: '100%',
																height: 'auto',
															}}
														/>
													);
												}
											})}
										</div>
									</div>
									<p className={styles.text}>{item.text}</p>
								</div>
							);
						})}
				</div>
				{reviewList && reviewList.length > 1 && (
					<button onClick={() => isToggleReviewsHandler(!isOpenReviews)} className={styles.showBtn}>
						{isOpenReviews ? t('product:reviewsBtn.hideComments') : t('product:reviewsBtn.viewAll')}
					</button>
				)}
				<div className={styles.paginationContainer}>
					{isOpenReviews &&
						totalPagesData > 1 &&
						[...Array(totalPagesData)].map((item, index) => {
							return (
								<button
									className={`${styles.paginationButton} ${
										currentPage === index + 1 ? styles.paginationButtonActive : ''
									}`}
									key={index}
									onClick={currentPage !== index + 1 ? () => paginationClickHandler(index + 1) : null}
								>
									{index + 1}
								</button>
							);
						})}
					{!reviewIsLoaded && <Loader imgWidth={18} />}
				</div>
			</>
		</div>
	);
};

export default React.memo(Reviews);
