import ProductItem from 'components/ProductItem';
import Button from 'components/UI/Button';
import Title from 'components/UI/Title';
import routes from 'helpers/routes';
import { useRouter } from 'next/router';
import React from 'react';
import { useSelector } from 'react-redux';
import DetailSidebarInfo from 'components/UI/DetailSidebarInfo';

import styles from './AddCheckoutSidebar.module.scss';

type AddCheckoutSidebarTypes = {
	data: {
		img: string;
		name: string;
		price: number;
		color: string;
		sizeList?: {
			name: string;
			id: number;
		}[];
		size?: string;
	};
	isOpen: boolean;
	onCloseInfo: () => void;
};

const AddCheckoutSidebar = ({ data, isOpen, onCloseInfo }: AddCheckoutSidebarTypes) => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const router = useRouter();

	return (
		<DetailSidebarInfo isOpen={isOpen} onCloseInfo={onCloseInfo}>
			<>
				<Title
					className={styles.title}
					withSuisse
					size={isMobile ? 22 : 28}
					text="Товар добавлен в корзину"
					marginBottom={40}
				/>
				<ProductItem className={styles.productItem} data={data} isFullInfo isReviwe />
				<div className={styles.buttonBlock}>
					<Button onClick={() => router.push(routes.checkout)} text="перейти в корзину" isInvisible isActive />
					<Button onClick={onCloseInfo} text="продолжить покупки" isInvisible />
				</div>
			</>
		</DetailSidebarInfo>
	);
};

export default React.memo(AddCheckoutSidebar);
