import Col from 'components/UI/Col';
import Title from 'components/UI/Title';

import { useTranslation } from 'next-i18next';
import React from 'react';
import { useSelector } from 'react-redux';
import DetailSidebarInfo from 'components/UI/DetailSidebarInfo';

import styles from './AboutProduct.module.scss';
import { SuisseIntl } from 'helpers/utils/fonts';

type AboutProductTypes = {
	article: string[];
	composition: string;
	podklad: string;
	productMeasurementsMockData: {
		name: string;
		sizeData: {
			name: string;
			size: string;
		}[];
	}[];
	delivery: string[];
	payment: string;
	refImage?: React.RefObject<HTMLDivElement>;
	chatRef: React.RefObject<HTMLDivElement>;
};

const AboutProduct = ({
	composition,
	podklad,
	productMeasurementsMockData,
	delivery,
	payment,
	refImage,
	article,
	chatRef,
}: AboutProductTypes) => {
	const [isOpenDetailInfo, setIsOpenDetailInfo] = React.useState<boolean>(false);
	const [isDeliveryInfo, setIsDeliveryInfo] = React.useState<boolean>(false);

	const colRef = React.useRef<HTMLDivElement | null>(null);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const { t } = useTranslation();

	const isOpenSideBarInfo = (isDelivery: boolean) => {
		setIsOpenDetailInfo(true);
		setIsDeliveryInfo(isDelivery);
		if (colRef && colRef.current) colRef.current.style.zIndex = '11';
		if (refImage && refImage.current) refImage.current.style.zIndex = '0';
		if (chatRef && chatRef.current) chatRef.current.style.zIndex = '1';
	};

	const onCloseInfoHandler = () => {
		setIsOpenDetailInfo(false);
		if (colRef && colRef.current) colRef.current.style.zIndex = '10';
		if (refImage && refImage.current) refImage.current.style.zIndex = '10000';
		if (chatRef && chatRef.current) chatRef.current.style.zIndex = '100000';
	};

	return (
		<Col colRef={colRef} className={styles.about} marginBottom={isMobile ? 24 : 40}>
			<>
				{article && (
					<>
						<p className={styles.title}>{t('product:article')}</p>
						<p className={`${styles.description} ${styles.firstDescription}`}>{article[0]}</p>
						<p className={styles.description}>{article[1]}</p>
					</>
				)}
				{(composition || podklad) && (
					<>
						<p className={styles.title}>{t('product:composition')}</p>
						<p className={`${styles.description} ${styles.firstDescription}`}>{composition}</p>
						<p className={styles.description}>{podklad}</p>
					</>
				)}
				{Array.isArray(productMeasurementsMockData) &&
					productMeasurementsMockData[0] &&
					productMeasurementsMockData[0].sizeData &&
					(productMeasurementsMockData[0].sizeData.length !== 0 ||
						productMeasurementsMockData[0].name == 'One size') && (
						<button onClick={() => isOpenSideBarInfo(false)} className={styles.infoBtn}>
							{t('product:productMeasurements')}
						</button>
					)}
				<button onClick={() => isOpenSideBarInfo(true)} className={styles.infoBtn}>
					{t('product:deliveryAndReturns')}
				</button>
				<DetailSidebarInfo onCloseInfo={onCloseInfoHandler} isOpen={isOpenDetailInfo}>
					{!isDeliveryInfo ? (
						<>
							<Title size={28} text={t('product:productMeasurements')} />
							<Col>
								<div className={styles.sizeBlock}>
									{Array.isArray(productMeasurementsMockData) &&
										productMeasurementsMockData.map(item => {
											return (
												<div key={item.name} className={styles.sizeItem}>
													<p className={styles.name}>{item.name}</p>
													{item.sizeData.map(itemSize => {
														return (
															<div key={itemSize.name} className={styles.sizeRow}>
																<p>{itemSize.name}</p>
																<p>{itemSize.size} см</p>
															</div>
														);
													})}
												</div>
											);
										})}
								</div>
							</Col>
						</>
					) : (
						<>
							<Title size={28} text={t('product:deliveryAndReturns')} />
							<Col>
								<>
									<p className={`${styles.titleDelivery} ${SuisseIntl.className}`}>Доставка</p>
									{delivery.map(item => (
										<p key={item} className={styles.textInfo}>
											{item}
										</p>
									))}
									<p className={`${styles.titleDelivery} ${SuisseIntl.className}`}>Оплата</p>
									<p className={styles.textInfo}>{payment}</p>
								</>
							</Col>
						</>
					)}
				</DetailSidebarInfo>
			</>
		</Col>
	);
};

export default React.memo(AboutProduct);
