import Product from 'components/ProductTile';
import Col from 'components/UI/Col';
import Title from 'components/UI/Title';

import { useTranslation } from 'next-i18next';
import { StaticImageData } from 'next/legacy/image';
import React from 'react';
import { useSelector } from 'react-redux';

import styles from './AlsoLike.module.scss';

type AlsoLikeTypes = {
	data: {
		id: number;
		title: string;
		size?: string[];
		colors?: string[];
		colorNames?: string[];
		activeColor: string;
		price: number;
		oldPrice?: number;
		isSale?: boolean;
		persentSale?: number;
		images: string[];
		secondImg?: string;
	}[];
};

const AlsoLike = ({ data }: AlsoLikeTypes) => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const { t } = useTranslation();

	return (
		<Col marginBottom={isMobile ? 32 : 60}>
			<>
				<Title size={isMobile ? 16 : 20} withDot text={t('product:youMayAlsoLike')} />
				<div
					style={{
						borderTop:
							(data.length < 4 && !isMobile) || (data.length < 2 && isMobile)
								? 'none'
								: '1px solid rgba(35, 35, 35, 0.3)',
					}}
					className={`${styles.product} ${
						(data.length < 4 && !isMobile) || (data.length < 2 && isMobile) ? styles.borderTop : ''
					}`}
				>
					{data.map(item => {
						return <Product key={item.id} data={item} />;
					})}
				</div>
			</>
		</Col>
	);
};

export default React.memo(AlsoLike);
