import Breadcrumbs from 'components/Breadcrumbs';
import Col from 'components/UI/Col';
import Title from 'components/UI/Title';

import routes from 'helpers/routes';
import React from 'react';
import ImagesBlock from '../ImagesBlock';
import fav from 'public/static/img/fav.svg';
import favActive from 'public/static/img/favActive.svg';

import styles from './Card.module.scss';
import { formattedPrice } from 'helpers/utils';
import ProductColorsList from '../ProductColorsList';
import ProductSizeList from '../ProductSizeList';
import Button from 'components/UI/Button';
import AboutProduct from '../AboutProduct';
import Reviews from '../Reviews';
import Image from 'next/image';
import Content from 'components/UI/Content';
import { useTranslation } from 'next-i18next';
import { useRouter } from 'next/router';
import { ProductDataModelAll } from 'api/product/models/ProductModel';
import AddCheckoutSidebar from '../AddCheckoutSidebar';
import { useSelector } from 'react-redux';
import { BreadcrumbsDataModel } from 'api/breadcrumbs/models/BreadcrumbsModel';
import AddProductApi from 'api/checkout/AddProductApi';
import { getCheckoutProduct } from 'store/product/CheckoutProductStore';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import FavoritesApi from 'api/favorites/FavoritesApi';
import { getFavoritesIds } from 'store/favorites/FavoritesStore';
import ym from 'react-yandex-metrika';
import SubTitle from 'components/UI/SubTitle/SubTitle';
import { IBMPlexMono } from 'helpers/utils/fonts';
import MarketplaceLinks from '../MarketplaceLinks/MarketplaceLinks';

type CardTypes = {
	chatRef: React.RefObject<HTMLDivElement>;
	mobileImage: React.RefObject<HTMLDivElement>;
	product: ProductDataModelAll;
	breadcrumbs: BreadcrumbsDataModel;
};

const addProductApi = new AddProductApi();
const favoritesStore = new FavoritesApi();

const Card = ({ chatRef, mobileImage, product, breadcrumbs }: CardTypes) => {
	const [chatIsBottom, setChatIsBottom] = React.useState<boolean>(false);
	const [checkoutSidebareIsOpen, setCheckoutSidebareIsOpen] = React.useState<boolean>(false);
	const [isProductInCheckout, setIsProductInCheckout] = React.useState<boolean>(false);
	const [isProductInFavorites, setIsProductInFavorites] = React.useState<boolean>(false);
	const [isRouting, setIsRouting] = React.useState<boolean>(false);

	const mobileTitleRef = React.useRef<HTMLDivElement | null>(null);
	const productInfo = React.useRef<HTMLDivElement | null>(null);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const checkoutProduct = useSelector((state: any) => state.checkoutProduct.checkoutProduct);
	const favoritesIds = useSelector((state: any) => state.favorites.favoritesIds);
	const token = useSelector((state: any) => state.userData.token);

	const router = useRouter();

	const dispatch = useAppDispatch();

	const { t } = useTranslation();

	const translateChatBtn = () => {
		const chat = chatRef?.current;
		const title = mobileTitleRef?.current;
		if (
			chat &&
			title &&
			chat.getBoundingClientRect().bottom + window.scrollY > title.getBoundingClientRect().top + window.scrollY
		) {
			if (!chatIsBottom) {
				chat.style.opacity = '0';
				setTimeout(() => {
					chat.style.top = 'auto';
					chat.style.bottom = '24px';
					setChatIsBottom(true);
					chat.style.opacity = '1';
				}, 300);
			}
		} else {
			if (chatIsBottom) {
				chat && (chat.style.opacity = '0');
				setTimeout(() => {
					setTimeout(() => {
						if (
							productInfo &&
							productInfo.current &&
							productInfo.current.getBoundingClientRect().top <= document.documentElement.clientHeight - 60
						) {
							chat &&
								(chat.style.top = `${
									mobileImage.current.getBoundingClientRect().bottom -
									(mobileImage.current.getBoundingClientRect().bottom - document.documentElement.clientHeight) -
									135 +
									window.scrollY
								}px`);
						} else {
							chat &&
								(chat.style.top = `${
									mobileImage.current.getBoundingClientRect().bottom - 65 + window.scrollY
								}px`);
						}
					}, 150);
					chat && (chat.style.bottom = 'auto');
					setChatIsBottom(false);
					chat && (chat.style.opacity = '1');
				}, 300);
			}
		}
	};

	const onScrollDocument = () => {
		if (productInfo && productInfo.current && mobileImage && mobileImage.current) {
			setTimeout(() => {
				if (mobileImage.current.getBoundingClientRect().bottom <= document.documentElement.clientHeight - 60) {
					productInfo.current.style.position = '';
					productInfo.current.style.width = '';
					productInfo.current.style.padding = '';
					productInfo.current.style.transform = '';
					productInfo.current.style.zIndex = '';
				}
			}, 100);
		}
	};

	const addCheckoutHandler = (isOpen: boolean) => {
		if (isProductInCheckout && isOpen) {
			router.push(routes.checkout);
			return;
		}
		if (isOpen === true) {
			addProductApi.addProduct({ id: parseInt(router.query.name as string) }).then(res => {
				if (res.data.message === 'success') {
					setCheckoutSidebareIsOpen(isOpen);
					setIsProductInCheckout(true);
					dispatch(getCheckoutProduct({ token: localStorage.getItem('token') || null }));
					process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID && ym('reachGoal', 'to-cart');
					process.env.NEXT_PUBLIC_IS_PRODUCTION === 'true' &&
						(window as any).dataLayer.push({
							ecommerce: {
								currencyCode: 'RUB',
								add: {
									products: [
										{
											id: product.id,
											name: product.name,
											category: breadcrumbs.map(item => item.name).join('/'),
											price: product.price,
											variant: product.currentColor,
										},
									],
								},
							},
						});
					return;
				}
			});
		} else {
			setCheckoutSidebareIsOpen(isOpen);
		}
	};

	const addToFavorites = () => {
		if (isProductInFavorites) {
			router.push(router.asPath + routes.favorites);
			return;
		}
		favoritesStore.addToFavorites({ id: product.id }).then(res => {
			if (res.data.message === 'ok') {
				process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID && ym('reachGoal', 'add-to-favorites');
				dispatch(getFavoritesIds({ token: localStorage.getItem('token') || null })).then(() => {
					setIsProductInFavorites(true);
					router.push(router.asPath + routes.favorites);
				});
			}
		});
	};

	React.useEffect(() => {
		document.addEventListener('scroll', translateChatBtn);
		return () => document.removeEventListener('scroll', translateChatBtn);
	}, [mobileTitleRef, chatRef, chatIsBottom]);

	React.useEffect(() => {
		if (productInfo && productInfo.current) {
			setTimeout(() => {
				if (window.scrollY > 100) return;
				if (productInfo.current.offsetTop > document.documentElement.clientHeight - 60) {
					productInfo.current.style.position = 'fixed';
					productInfo.current.style.top = document.documentElement.clientHeight - 70 + 'px';
					productInfo.current.style.width = '100vw';
					productInfo.current.style.padding = '12px';
					productInfo.current.style.transform = 'translate(-12px)';
					productInfo.current.style.zIndex = '10000';
				}
			}, 100);
		}

		document.addEventListener('scroll', onScrollDocument);
	}, [Object.values(productInfo)]);

	React.useEffect(() => {
		setIsProductInCheckout(checkoutProduct.some(item => item === product.id));
	}, [checkoutProduct, product]);

	React.useEffect(() => {
		setIsProductInFavorites(favoritesIds.some(item => item === product.id));
	}, [favoritesIds, product]);

	React.useEffect(() => {
		(window as any).dataLayer = (window as any).dataLayer || [];
		process.env.NEXT_PUBLIC_IS_PRODUCTION === 'true' &&
			(window as any).dataLayer.push({
				ecommerce: {
					currencyCode: 'RUB',
					detail: {
						products: [
							{
								id: product.id,
								name: product.name,
								category: breadcrumbs.map(item => item.name).join('/'),
								price: product.price,
								variant: product.currentColor,
							},
						],
					},
				},
			});
	}, []);

	const redirectToMarketplace = (url: string) => {
		if (!url.startsWith('https://')) {
			window.open(`https://${url}`, '_blank');
		} else {
			window.open(url, '_blank');
		}
	};

	return (
		<>
			{!isMobile ? (
				<Content className={styles.content}>
					<Col marginBottom={isMobile ? 60 : 80} className={styles.cardCol}>
						<div className={styles.card}>
							<ImagesBlock refImage={mobileImage} images={product.images} discount={product.discount} />
							<div className={styles.productInfo}>
								<Col marginBottom={8}>
									<Breadcrumbs isProduct links={breadcrumbs} />
								</Col>
								<div ref={mobileTitleRef} className={styles.nameAndPrice}>
									<Title
										withSuisse
										size={isMobile ? 22 : 28}
										text={product.name}
										className={styles.productTitle}
									/>
									<div className={styles.priceBlock}>
										<Title
											withSuisse
											marginBottom={0}
											size={isMobile ? 22 : 28}
											text={formattedPrice(product.price)}
											className={styles.productTitle}
										/>
										{product.discount && (
											<SubTitle
												marginTop={4}
												size={isMobile ? 12 : 16}
												text={formattedPrice(product.oldPrice)}
												className={styles.productTitle}
											/>
										)}
									</div>
								</div>
								<ProductColorsList colorsList={product.colors} currentColor={product.currentColor} />
								{product.size && product.size.length !== 0 && (
									<ProductSizeList
										chatRef={chatRef}
										refImage={mobileImage}
										sizeList={product.size}
										breadcrumbs={breadcrumbs}
										sizeLabel={product.sizeLabel}
										currentSize={product.currentSize}
									/>
								)}

								<Col marginBottom={isMobile ? 24 : 40}>
									<div className={styles.buttons}>
										<Button
											onClick={() => addCheckoutHandler(true)}
											disabled={isRouting}
											isActive
											text={!isProductInCheckout ? t('product:addToCart') : t('product:goToCart')}
										/>
										<Button
											isInvisible
											text={
												isProductInFavorites
													? t('product:addedToFavourites')
													: t('product:addToFavourites')
											}
											onClick={token ? addToFavorites : () => router.push(router.asPath + routes.signIn)}
											disabled={isRouting}
										/>
									</div>
								</Col>
								{breadcrumbs && breadcrumbs[0]?.code === 'deti' && (
									<MarketplaceLinks
										isMobile={isMobile}
										ozon={product?.links?.ozon || ''}
										wildberries={product?.links?.wildberries || ''}
										isOzon={product?.links?.ozon?.length > 0}
										isWilberries={product?.links?.wildberries?.length > 0}
									/>
								)}
								{/*<PaymentByShares />*/}
								<AboutProduct
									chatRef={chatRef}
									refImage={mobileImage}
									delivery={product.delivery}
									payment={product.payment}
									productMeasurementsMockData={product.measurement}
									composition={product.composition}
									article={product.article}
									podklad={product.podklad}
								/>
								{product.reviews &&
									Array.isArray(product.reviews.reviewsList) &&
									product.reviews.reviewsList.length !== 0 && (
										<Reviews
											counter={product.reviews.counter}
											totalStars={product.reviews.totalStars}
											reviewsList={product.reviews.reviewsList}
											totalPages={product.reviews.totalPages}
										/>
									)}
							</div>
						</div>
					</Col>
				</Content>
			) : (
				<Col marginBottom={isMobile ? 60 : 80} className={styles.cardCol}>
					<div className={styles.card}>
						<ImagesBlock refImage={mobileImage} images={product.images} discount={product.discount} />
						<div ref={productInfo} className={styles.productInfo}>
							<Col marginBottom={8}>
								<Breadcrumbs links={breadcrumbs} />
							</Col>
							<div ref={mobileTitleRef} className={styles.nameAndPrice}>
								<Title withSuisse size={isMobile ? 22 : 28} text={product.name} />
								<div className={styles.priceBlock}>
									<Title
										withSuisse
										marginBottom={0}
										size={isMobile ? 22 : 28}
										text={formattedPrice(product.price)}
									/>
									{product.discount && (
										<SubTitle
											marginTop={4}
											size={isMobile ? 12 : 16}
											text={formattedPrice(product.oldPrice)}
											className={styles.productTitle}
										/>
									)}
								</div>
							</div>
							{product.colors && product.colors.length !== 0 && (
								<ProductColorsList colorsList={product.colors} currentColor={product.currentColor} />
							)}
							{product.size && product.size.length !== 0 && (
								<ProductSizeList
									chatRef={chatRef}
									refImage={mobileImage}
									sizeList={product.size}
									breadcrumbs={breadcrumbs}
									sizeLabel={product.sizeLabel}
									currentSize={product.currentSize}
								/>
							)}
							<Col marginBottom={isMobile ? 24 : 40}>
								<div className={styles.buttons}>
									<Button
										onClick={() => addCheckoutHandler(true)}
										isActive
										text={!isProductInCheckout ? t('product:addToCart') : t('product:goToCart')}
									/>
									<Button
										isInvisible
										onClick={token ? addToFavorites : () => router.push(router.asPath + routes.signIn)}
									>
										<Image
											alt="fav"
											src={isProductInFavorites ? favActive : fav}
											width={24}
											height={24}
											style={{
												maxWidth: '100%',
												height: 'auto',
											}}
										/>
									</Button>
								</div>
							</Col>
							{/*<PaymentByShares />*/}
							{breadcrumbs && breadcrumbs[0]?.code === 'deti' && (
								<MarketplaceLinks
									isMobile={isMobile}
									ozon={product?.links?.ozon || ''}
									wildberries={product?.links?.wildberries || ''}
									isOzon={product?.links?.ozon?.length > 0}
									isWilberries={product?.links?.wildberries?.length > 0}
								/>
							)}
							<AboutProduct
								chatRef={chatRef}
								refImage={mobileImage}
								delivery={product.delivery}
								payment={product.payment}
								productMeasurementsMockData={product.measurement}
								composition={product.composition}
								article={product.article}
								podklad={product.podklad}
							/>
							{product.reviews &&
								Array.isArray(product.reviews.reviewsList) &&
								product.reviews.reviewsList.length !== 0 && (
									<Reviews
										counter={product.reviews.counter}
										totalStars={product.reviews.totalStars}
										reviewsList={product.reviews.reviewsList}
										totalPages={product.reviews.totalPages}
									/>
								)}
						</div>
					</div>
				</Col>
			)}
			<AddCheckoutSidebar
				data={{
					img: product?.images[0],
					name: product?.name,
					price: product?.price,
					color: product?.currentColor,
					sizeList: product?.size,
					size: product?.currentSize,
				}}
				isOpen={checkoutSidebareIsOpen}
				onCloseInfo={() => addCheckoutHandler(false)}
			/>
		</>
	);
};

export default React.memo(Card);
