import ProductCard from 'components/pages/ProductCard';
import { serverSideTranslations } from 'next-i18next/serverSideTranslations';
import React from 'react';
import dynamic from 'next/dynamic';
import Footer from 'components/Footer';
import Header from 'components/Header';
import ProductApi from 'api/product/ProductApi';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { changeIsMobile } from 'store/isMobile/IsMobileStore';
import BreadcrumbsApi from 'api/breadcrumbs/Breadcrumbs';
import AlsoLikeApi from 'api/alsoLike/AlsoLike';
import OftenBuyApi from 'api/oftenBuy/OftenBuy';
import { useGetUserData } from 'helpers/hooks/useGetUserData';
import { baseSSR } from 'helpers/utils/baseSSR';
import Head from 'next/head';
import MetaTagsApi from 'api/seo/MetaTagsApi';

const ProductPage = ({ data }) => {
	const chatRef = React.useRef<HTMLDivElement | null>(null);
	const mobileImage = React.useRef<HTMLDivElement | null>(null);
	const dispatch = useAppDispatch();

	useGetUserData();

	dispatch(changeIsMobile(data.isMobile));

	const ChatButton = dynamic(() => import('components/ChatButton'));
	return (
		<>
			<Head>
				<title>
					{data.metaTags && data.metaTags.title
						? data.metaTags.title
						: 'Интернет магазин одежды российского бренда TOTTI'}
				</title>
				<meta
					name="description"
					content={data.metaTags && data.metaTags.description ? data.metaTags.description : data.product.name}
				></meta>
			</Head>
			<Header />
			<ProductCard
				breadcrumbs={data.breadcrumbs}
				alsoLike={data.alsoLike}
				oftenBuy={data.oftenBuy}
				product={data.product}
				mobileImage={mobileImage}
				chatRef={chatRef}
			/>
			<ChatButton chatRef={chatRef} mobileImage={mobileImage} isNotOpacity inCard={data.isMobile} />
			<Footer breadcrumbs={data.breadcrumbs} withDarkBGC={true} />
		</>
	);
};

export const getServerSideProps = async ({ req, params, locale }) => {
	const { isMobile } = await baseSSR(req);
	const productApi = new ProductApi();
	const breadcrumbsApi = new BreadcrumbsApi();
	const alsoLikeApi = new AlsoLikeApi();
	const oftenBuyApi = new OftenBuyApi();
	const metaTagsApi = new MetaTagsApi();

	const [product, breadcrumbs, alsoLike, oftenBuy, metaTags] = await Promise.all([
		productApi.getProductData({ id: params.name }),
		breadcrumbsApi.getBreadcrumbsData({ productId: params.name }),
		alsoLikeApi.getAlsoLike({ id: params.name }),
		oftenBuyApi.getOftenBuy({ id: params.name }),
		metaTagsApi.getMetaTags({ productId: params.name }),
	]);

	if (product.data.message) {
		return {
			notFound: true,
		};
	}
	return {
		props: {
			data: {
				breadcrumbs: breadcrumbs.data,
				product: product.data,
				alsoLike: alsoLike.data,
				oftenBuy: oftenBuy.data,
				metaTags: metaTags.data,
				isMobile,
			},
			...(await serverSideTranslations(locale, ['common', 'product'])),
		},
	};
};

export default ProductPage;
