import Col from 'components/UI/Col';
import routes from 'helpers/routes';
import { useTranslation } from 'next-i18next';
import Image, { StaticImageData } from 'next/image';
import Link from 'next/link';
import React from 'react';

import styles from './ProductColorsList.module.scss';

type ProductColorsListTypes = {
	colorsList: {
		id: number;
		name: string;
		url: string | StaticImageData;
	}[];
	currentColor: string;
};

const ProductColorsList = ({ colorsList, currentColor }: ProductColorsListTypes) => {
	const [currentColorState, setCurrentColor] = React.useState<string>(currentColor);

	const { t } = useTranslation();

	const onClickColorHandler = (event: React.MouseEvent<HTMLDivElement>) => {
		const target = event.currentTarget;

		setCurrentColor(target.id);
	};

	return (
		<Col marginBottom={24}>
			<div className={styles.colors}>
				<p>
					{t('product:color')}: {colorsList.find(item => item.name === currentColorState)?.name}
				</p>
				<div className={styles.images}>
					{colorsList.map(item => {
						return (
							<Link key={item.name} href={routes.productCard + item.id}>
								<div
									style={item.name === currentColorState ? { borderColor: '#1B2530' } : {}}
									className={styles.img}
									onClick={onClickColorHandler}
									id={item.name}
								>
									<Image
										alt={item.name}
										src={item.url}
										onLoadingComplete={img => (img.style.backgroundImage = 'none')}
										fill
										priority
									/>
								</div>
							</Link>
						);
					})}
				</div>
			</div>
		</Col>
	);
};

export default React.memo(ProductColorsList);
