import React from 'react';

import styles from './DetailSidebarInfo.module.scss';
import Image from 'next/image';

import closeDark from 'public/static/img/closeDark.svg';
import { useRouter } from 'next/router';

type DetailSidebarInfoTypes = {
	isOpen: boolean;
	children: React.ReactElement<any, any>;
	onCloseInfo: () => void;
};

const DetailSidebarInfo = ({ isOpen, children, onCloseInfo }: DetailSidebarInfoTypes) => {
	const infoRef = React.useRef<HTMLDivElement | null>(null);

	const router = useRouter();

	const onTouchmove = (event: TouchEvent) => {
		event.preventDefault();
	};

	const onCloseDetails = (event: React.MouseEvent<HTMLDivElement>) => {
		const { target } = event;

		if (!infoRef.current?.contains(target as Node)) {
			onCloseInfo();
		}
	};

	React.useEffect(() => {
		document.querySelector('body').style.marginRight = isOpen
			? `${window.innerWidth - document.documentElement.clientWidth}px`
			: '0';
		document.querySelector<HTMLDivElement>('header').style.width = isOpen
			? `calc(100vw - ${infoRef.current.clientWidth + (window.innerWidth - document.documentElement.clientWidth)}px)`
			: '';
		document.querySelector<HTMLDivElement>('header > div:first-child').style.width = isOpen ? '100vw' : '100%';
		document.querySelector<HTMLDivElement>('header').style.overflow = isOpen ? 'hidden' : '';
		document.querySelector<HTMLDivElement>('header').style.borderBottom = isOpen ? 'none' : '';
		document.querySelector<HTMLDivElement>('header > div:first-child').style.backgroundColor = isOpen ? '#535351' : '#F3F3EA';
		if (!router.query['personal-area']) {
			document.querySelector<HTMLBodyElement>('body').style.overflow = isOpen ? 'hidden' : 'hidden scroll';
			document.querySelector<HTMLBodyElement>('body').style.touchAction = isOpen ? 'none' : '';
		}
		document.addEventListener('touchmove', onTouchmove);
		return () => document.removeEventListener('touchmove', onTouchmove);
	}, [isOpen, infoRef, router]);

	return (
		<>
			{isOpen && (
				<div onClick={onCloseDetails} className={styles.detailsBGC}>
					<div ref={infoRef} className={styles.details}>
						<Image
							alt="X"
							onClick={onCloseInfo}
							src={closeDark}
							width={24}
							height={24}
							style={{
								maxWidth: '100%',
								height: 'auto',
							}}
						/>
						{children}
					</div>
				</div>
			)}
		</>
	);
};

export default React.memo(DetailSidebarInfo);
