import { AlsoLikeDataModel } from 'api/alsoLike/models/AlsoLikeModel';
import { BreadcrumbsDataModel } from 'api/breadcrumbs/models/BreadcrumbsModel';
import { OftenBuyDataModel } from 'api/oftenBuy/models/OftenBuyModel';
import { ProductDataModelAll } from 'api/product/models/ProductModel';
import ActiveLink from 'components/ActiveLink';
import Button from 'components/UI/Button';
import Col from 'components/UI/Col';
import Content from 'components/UI/Content';
import routes from 'helpers/routes';
import React from 'react';

import styles from './ProductCard.module.scss';
import AlsoLike from './views/AlsoLike';
import Card from './views/Card';
import OftenBuy from './views/OftenBuy';

type ProductCardTypes = {
	chatRef: React.RefObject<HTMLDivElement>;
	mobileImage: React.RefObject<HTMLDivElement>;
	breadcrumbs: BreadcrumbsDataModel;
	product: ProductDataModelAll;
	alsoLike: AlsoLikeDataModel;
	oftenBuy: OftenBuyDataModel;
};

const ProductCard = ({ chatRef, mobileImage, product, alsoLike, oftenBuy, breadcrumbs }: ProductCardTypes) => {
	return (
		<>
			<Card breadcrumbs={breadcrumbs} product={product} mobileImage={mobileImage} chatRef={chatRef} />
			<Content>
				<>
					{oftenBuy && Array.isArray(oftenBuy.products) && oftenBuy.products.length > 0 && (
						<OftenBuy data={oftenBuy.products} />
					)}
					{alsoLike && Array.isArray(alsoLike.products) && alsoLike.products.length > 0 && (
						<AlsoLike data={alsoLike.products} />
					)}
					<Col marginBottom={60}>
						<ActiveLink
							href={
								routes.catalog +
								(breadcrumbs.length > 3 ? breadcrumbs[2].code : breadcrumbs[breadcrumbs.length - 1].code)
							}
						>
							<Button className={styles.button} text={'посмотреть все'} isInvisible />
						</ActiveLink>
					</Col>
				</>
			</Content>
		</>
	);
};

export default React.memo(ProductCard);
