import Col from 'components/UI/Col';
import { IBMPlexMono } from 'helpers/utils/fonts';
import React from 'react';
import styles from '../Card/Card.module.scss';
import Button from 'components/UI/Button';

type Props = {
	ozon?: string;
	isOzon: boolean;
	isWilberries: boolean;
	wildberries?: string;
	isMobile: boolean;
};

const MarketplaceLinks = ({ ozon, wildberries, isMobile, isWilberries, isOzon }: Props) => {
	const redirectToMarketplace = (url: string) => {
		if (!url.startsWith('https://')) {
			window.open(`https://${url}`, '_blank');
		} else {
			window.open(url, '_blank');
		}
	};

	return (
		(isOzon || isWilberries) && (
			<Col marginBottom={isMobile ? 24 : 40}>
				<div className={styles.buy_on}>
					<span className={`${IBMPlexMono.className}`}>Купить на</span>
					<div className={styles.buttons_mt}>
						{isOzon && (
							<Button
								withoutLoader
								transBorder
								isInvisible
								onClick={() => {
									redirectToMarketplace(ozon);
								}}
								text={'OZON'}
							/>
						)}
						{isWilberries && (
							<Button
								withoutLoader
								transBorder
								isInvisible
								text={'WILDBERRIES'}
								onClick={() => {
									redirectToMarketplace(wildberries);
								}}
							/>
						)}
					</div>
				</div>
			</Col>
		)
	);
};

export default MarketplaceLinks;
