import Image from 'next/image';
import React from 'react';

import leftArrow from 'public/static/img/leftArrow.svg';
import rightArrow from 'public/static/img/rightArrow.svg';
import styles from './ImagesBlock.module.scss';
import { useRouter } from 'next/router';
import { useSelector } from 'react-redux';

type ImagesBlockTypes = {
	images: string[];
	discount: number;
	refImage?: React.RefObject<HTMLDivElement>;
};

const ImagesBlock = ({ images, discount, refImage }: ImagesBlockTypes) => {
	const [currentImg, setCurrentImg] = React.useState<number>(0);
	const [currentImgScroll, setCurrentImgScroll] = React.useState<number>(currentImg);
	const [left, setLeft] = React.useState<number>(0);
	const [drag, setDrag] = React.useState<boolean>(false);

	const scroll = React.useRef<HTMLDivElement>(null);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const router = useRouter();

	const translateImg = (isLeft, ref) => {
		if (isLeft && currentImg === 0) {
			setCurrentImg(images.length - 1);
			setCurrentImgScroll(images.length - 1);
			ref.current.style.transform = `translateX(-${document.documentElement.scrollWidth * (images.length - 1)}px)`;
		} else if (!isLeft && currentImg === images.length - 1) {
			setCurrentImg(0);
			setCurrentImgScroll(0);
			ref.current.style.transform = `translateX(-${document.documentElement.scrollWidth * 0}px)`;
		} else {
			setCurrentImg(isLeft ? currentImg - 1 : currentImg + 1);
			setCurrentImgScroll(isLeft ? currentImg - 1 : currentImg + 1);
			ref.current.style.transform = `translateX(-${
				document.documentElement.scrollWidth * (isLeft ? currentImg - 1 : currentImg + 1)
			}px)`;
		}
	};

	const onTouchdownHandler = (event: React.TouchEvent<HTMLDivElement>) => {
		setDrag(true);
		setLeft(event.touches[0].pageX);
	};

	const onTouchEndHandler = (event: React.TouchEvent<HTMLDivElement>) => {
		setDrag(false);
		if (left - event.changedTouches[0].pageX < 10 && left - event.changedTouches[0].pageX > -10) return;
		if (images.length - 1 === currentImgScroll && left > event.changedTouches[0].pageX) return;
		if (0 === currentImgScroll && left < event.changedTouches[0].pageX) return;
		event.currentTarget.style.transform = `translateX(-${document.documentElement.scrollWidth * currentImg}px)`;
		setCurrentImgScroll(left > event.changedTouches[0].pageX ? currentImgScroll + 1 : currentImgScroll - 1);
	};

	const onTouchMoveHandler = (event: React.TouchEvent<HTMLDivElement>) => {
		if (drag) {
			if (images.length - 1 === currentImg && left > event.targetTouches[0].pageX) return;
			if (0 === currentImg && left < event.targetTouches[0].pageX) return;
			setCurrentImg(left > event.targetTouches[0].pageX ? currentImgScroll + 1 : currentImgScroll - 1);
		}
	};

	React.useEffect(() => {
		setCurrentImg(0);
		setCurrentImgScroll(0);
		setLeft(0);
		if (scroll && scroll.current) scroll.current.style.transform = 'translateX(0)';
	}, [router.query.name]);

	return (
		<>
			{isMobile ? (
				<div ref={refImage} className={styles.mobileImages}>
					<Image
						alt="<"
						onClick={() => translateImg(true, scroll)}
						src={leftArrow}
						width={24}
						height={24}
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
					<div
						style={scroll.current ? { height: `${scroll.current.clientHeight}px` } : {}}
						className={styles.imagesScrollcontainer}
					>
						{/*{discount && <div className={styles.sale}>Sale</div>}*/}
						{discount && <div className={styles.sale}>- {discount}%</div>}
						<div
							ref={scroll}
							onTouchStart={onTouchdownHandler}
							onTouchEnd={onTouchEndHandler}
							onTouchMove={onTouchMoveHandler}
							style={{
								width: `calc(${images.length} * 100%)`,
								height: scroll.current ? `${scroll.current.clientHeight}px` : '',
							}}
							className={styles.imagesScrollBlock}
						>
							{images.map((item, index) => {
								return (
									<Image
										alt="img"
										key={item}
										src={item}
										onLoadingComplete={img => (img.style.backgroundImage = 'none')}
										sizes="100vw"
										fill
									/>
								);
							})}
						</div>
					</div>
					<div className={styles.dotList}>
						{[...Array(images.length)].map((item, index) => {
							return <div key={item} style={currentImg !== index ? { opacity: '0.2' } : { opacity: '1' }}></div>;
						})}
					</div>
					<Image
						alt=">"
						onClick={() => translateImg(false, scroll)}
						src={rightArrow}
						width={24}
						height={24}
						priority
						quality={100}
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
				</div>
			) : (
				<div className={styles.images}>
					{/*{discount && <div className={styles.sale}>Sale</div>}*/}
					{discount && <div className={styles.sale}>- {discount}%</div>}
					{images.map((item, index) => {
						return (
							<div
								style={{ borderBottom: index === images.length - 1 ? '1px solid rgba(35, 35, 35, 0.3)' : 'none' }}
								key={item}
							>
								<Image
									alt="img"
									src={item}
									sizes="100vw"
									fill
									quality={80}
									onLoadingComplete={img => (img.style.backgroundImage = 'none')}
									style={{
										//width: '100%',
										//height: 'auto',
										display: 'block',
									}}
								/>
							</div>
						);
					})}
				</div>
			)}
		</>
	);
};

export default React.memo(ImagesBlock);
